<template>
  <div class="title">
    <!-- <h1>{{ msg }}</h1> -->
    <span>{{ mes }}</span>
    <p>{{ mes }}</p>
    <theme-picker></theme-picker>
    <el-button type="primary">提交</el-button>
  </div>
</template>

<script>
import theme from "../utils/theme";
export default {
  data() {
    return {
      theme: theme["DEFAULT"],
      msg: "welcome to your cart page",
      mes: "cart-page",
      componentList: [
        {
          componentName: "xx",
          config: {
            color: "xxx",
            background: "xxx"
          }
        }
      ]
    };
  },
  methods: {
    changeTheme() {
      this.theme = theme["FIRST"];
    }
  }
};
</script>

<style scoped>
span {
  font-size: 1em;
}
p {
  font-size: 16px;
}
div{
  position: static;
}
</style>



